.myWorksWrapper {
    text-align: center;
    min-height: 100vh;
}
.myWorksContainer {
    display: inline-block;
    margin: 10px 0;
    position: relative;
}
.myWorksContainer:before {
    content: "";
    position: absolute;
    background: #f5df4e;
    width: 80px;
    height: 3px;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,50%);
}
.wrapperForBlock {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top:8rem;
}
.wrapperForBlockContainer {
    display: flex;
    flex: 0 1 30%;
    width: 20%;
    padding: 0 10px;
    margin-bottom: 30px;
}
.blockContainerWorks {
    display: flex;
    flex-direction: column;
    width: 100%;
    transition: 0.3s;
}
.blockContainerWorks:hover {
    box-shadow: 0 0 10px rgb(0 0 0 / 50%);
    transition: 0.3s;
}
.imageContainerWorks {
    width: 50%;
    margin: 0 auto;
    height: 50px;
}
.titleBlockWorks {
    display: flex;
    justify-content: center;
    height: 15rem;
    font-weight: 500;
    font-size: 2.1rem;
    line-height: 2.5rem;
    letter-spacing: 0.05em;
    color: #000;
    background: url('data:image/jpeg;base64,') center no-repeat;
    background-size: cover;
    border-bottom: 5px solid #ffb400;
}
.textBlockWorks {
    font-size: 1.4rem;
    line-height: 2rem;
    padding: 0 3rem;
    flex: 1 1 auto;
}
.textBlockWorks {
    padding: 0 0.5rem;
}
.text {
    font-size: 1.4rem;
    line-height: 2rem;
    background: white;
    padding: 1rem;
    text-align: initial;
}


@media (max-width: 1190px) {
    .wrapperForBlock {
        flex-wrap: wrap;
        margin: 0;
    }
    .wrapperForBlockContainer {
        display: flex;
        flex: 0 1 350px;
        width: 20%;
        padding: 0 10px;
        margin-bottom: 30px;
    }
}

