.wrapperContacts {
    background-color: #f5df4e;
    min-height: 100vh;
    text-align: center;
    padding-top: 3rem;
}
.containerContacts {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 40rem;
}
.contactTitle{
    position: relative;
}
.contactTitle:before {
    content: "";
    position: absolute;
    background: black;
    width: 80px;
    height: 3px;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,50%);
}
.formContacts {
    flex-direction: column;
    display: inline-block;
    width: 30rem;
}
.inputContact {
    color: #000000;
    border: 1px solid #111;
    resize:none;
    width: 100%;
    padding: 12px 26px;
    height: 20px;
    border-radius: 30px;
    transition: .3s;
}
.inputContact:hover {
    border-color: #f5df4e;
}
.textAreaContact {
    color: #000000;
    border: 1px solid #111;
    resize:none;
    width: 100%;
    padding: 12px 26px;
    height: 200px;
    border-radius: 30px;
    transition: .3s;
}
.textAreaContact:hover {
    border-color: #f5df4e;
}
.secondDivInput {
    padding: 1rem 0;
}

@media (max-width: 570px) {
    .formContacts {
        width: 15rem;
    }
    .containerContacts {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        border: 1px solid black;
        height: 25rem;
    }
}