.blockFirst {
    background: #f5df4e;
}
.textAboutMe {
    font-size: 51px;
    line-height: 62px;
    font-weight: 700;
    margin: 18px 0 10px;
    position: relative;
    padding-left: 70px;
}
.textAboutMe:before {
    content: "";
    position: absolute;
    left: 0;
    top: 30px;
    height: 4px;
    width: 40px;
    border-radius: 10px;
    background-color: black;
}
.openTextAboutMe {
    font-size: 40px;
    line-height: 62px;
    font-weight: 700;
    margin: 18px 0 10px;
    position: relative;
    padding-left: 70px;
    color: #ffffff;
}
.openTextAboutMe span{
    margin: 15px 0 28px;
    font-size: 20px;
    line-height: 35px;
    color: black;
}
.itemContainer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 30rem;
}
.imgContainer {
    width: 250px;
    height: 250px;
    margin: 0 auto;
    overflow: hidden;


    transform: translate(150%,0px);
    opacity: 0;
    transition: all 0.8s ease 0s;
}
.activeImgContainer{
    width: 250px;
    height: 250px;
    margin: 0 auto;
    overflow: hidden;


    transform: translate(0px,0px);
    opacity: 1;
    transition: all 0.8s ease 0s;
}
.text{
    display: flex;
    flex-direction: column;
    font-weight: 700;
    width: 40%;

    transform: translate(-120%,0px);
    opacity: 0;
    transition: all 0.8s ease 0s;


}
.activeText{
    display: flex;
    flex-direction: column;
    font-weight: 700;
    width: 40%;

    opacity: 1;
    transform: translate(0px,0px);
    transition: all 0.8s ease 0s;
}
@media (max-width: 870px) {
    .text {
        margin: 0;
        width: 230px;
    }
    .activeText {
        margin: 0;
        width: 230px;
    }
    .blockFirst {
        min-height: 530px;
    }
    .itemContainer {
        flex-direction: column;
    }
}


