*,
*::before,
*::after {
    box-sizing: border-box;
}
img {
    border-radius: 50%;
    width: 100%;
    max-width: 100%;
    height: 100%;
    margin: 0 auto;
}
.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow-x: hidden;
}

.main {
    flex: 1 0 auto;
}
.footer {
    flex: 0 0 auto;
}
a {
    text-decoration: none;
}
ul {
    list-style-type: none
}
.container {
    width: 100%;
    max-width: 1191px;
    padding: 0 15px;
    margin: 0 auto;
}
h1,h2 {
    font-family: 'Poppins', sans-serif;
}
p {
    font-family: 'Open Sans', sans-serif;
}